@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  width: 100%;
  font-size: 14px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  line-height: 1.2;
}
.page__body {
  min-width: 100%;
  margin: auto;
  min-height: 100%;
  background-color: #ffffff;
  color: #283645;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.page-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
  width: 100%;
  z-index: 5;
  margin-bottom: -70px;
}
@media (min-width: 660px) {
  .page-header {
    margin-bottom: -86px;
  }
}
@media (min-width: 960px) {
  .page-header {
    position: absolute;
  }
}
.navigation {
  width: 100%;
  margin: auto;
  position: relative;
  min-height: 70px;
}
.navigation--open {
  background-color: #283645;
}
@media (min-width: 960px) {
  .navigation {
    max-width: 960px;
    background: none;
  }
}
@media (min-width: 960px) {
  .navigation__wrap {
    max-width: 960px;
    display: grid;
    grid-template-columns: 1fr 3fr;
    column-gap: 90px;
    align-items: center;
    align-self: center;
    padding: 0 20px;
  }
}
.navigation--open .navigation__list {
  border-top: 1px solid #000000;
}
@media (min-width: 960px) {
  .navigation--open .navigation__list {
    border: none;
  }
}
.navigation__logo {
  display: inline-block;
  vertical-align: middle;
  padding: 20px 15px;
}
.navigation__logo-img {
  width: 74px;
  height: 23px;
}
@media (min-width: 660px) {
  .navigation__logo-img {
    width: 145px;
    height: 40px;
  }
}
@media (min-width: 960px) {
  .navigation__logo-img {
    width: 147px;
    height: 40px;
  }
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (min-width: 960px) {
  .navigation__list {
    min-width: 620px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
.navigation__link {
  display: block;
  color: #ffffff;
  background-color: #283645;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.4;
  padding: 25px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px solid #000000;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  position: relative;
}
@media (min-width: 960px) {
  .navigation__link {
    border: none;
    text-transform: capitalize;
    font-weight: normal;
    line-height: 1;
    background: none;
  }
}
.navigation__toggle {
  position: absolute;
  right: 14px;
  top: 50%;
  display: block;
  width: 50px;
  height: 24px;
  background: none;
  cursor: pointer;
  border: none;
  transform: translateY(-50%);
}
@media (min-width: 960px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 7px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.navigation--open .navigation__toggle {
  top: 35px;
  left: auto;
  right: 14px;
}
.navigation--open .navigation__toggle::before,
.navigation--open .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 12px;
  width: 23px;
  height: 2px;
  background-color: #ffffff;
}
.navigation--open .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--open .navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .navigation--open .navigation__toggle {
    top: 42px;
  }
}
.navigation--closed .navigation__item {
  display: none;
}
.navigation--nojs .navigation__toggle {
  display: none;
}
